@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Quicksand:wght@300;400;500;600;700&display=swap);
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}

.Wordify-root {
    background: #fff;
    font-family: "Quicksand", sans-serif;
    font-size: 1rem;
    padding: 1rem;
}

.Wordify-editor {
    border-top: 0.125rem solid #ddd;
    cursor: text;
    font-size: 1rem;
    margin-top: 0.5rem;
}

.Wordify-editor .public-DraftEditorPlaceholder-root,
.Wordify-editor .public-DraftEditor-content {
    margin: 0 -1rem -1rem;
    padding: 1rem;
}

.Wordify-editor .Wordify-blockquote {
    border-left: 0.25rem solid #eee;
    color: #666;
    font-family: "Quicksand", sans-serif;
    font-style: italic;
    margin: 1rem 0;
    padding: 0.5rem 1rem;
}

.Wordify-editor .public-DraftStyleDefault-pre {
    background-color: rgba(0, 0, 0, 0.05);
    font-family: "Fira Code", monospace;
    font-size: 1rem;
    padding: 1rem;
}

.Wordify-controls {
    font-family: "Quicksand", sans-serif;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Wordify-styleButton {
    color: #999;
    cursor: pointer;
    margin-right: 1rem;
    padding: 0.125rem 0;
    display: inline-block;
}

.Wordify-activeButton {
    color: #03A9F4;
}
